import React, { useEffect, useState, lazy } from 'react';
import axios from 'axios';
import Box from '@material-ui/core/Box';
import { makeStyles } from '@material-ui/core';
import { useHistory, useLocation } from 'react-router-dom';
import IconButton from '@material-ui/core/IconButton';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Auth from '../../utils/Auth';
import Loader from '../../components/Loader';

const Candidate = lazy(() => import('../Candidate'));
const Recruiter = lazy(() => import('../Recruiter'));
const Admin = lazy(() => import('../Admin'));
const NavBar = lazy(() => import('../../components/NavBar'));
const GuestWelcome = lazy(() => import('../GuestWelcome'));



const useStyles = makeStyles(theme => ({
  root: {
    minHeight: '80vh',
  },
  menuButton: {

  }
}));

function Home() {
  const classes = useStyles();
  const history = useHistory();
  const location = useLocation();

  const [user, setUser] = useState({});

  const [anchorEl, setAnchorEl] = useState(null);
  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = (isLogoutClicked) => {
    if (isLogoutClicked) {
      // do logout;
      Auth.removeToken();
      setUser({});
    }
    setAnchorEl(null);
  };

  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const token = Auth.getToken();
    if (!token) {
      setIsLoading(false);
      delete axios.defaults.headers.common['Authorization'];
      // redirect to login page
      if (location.pathname !== '/') {
        history.push('/signin');
      }
    } else {
      // login with token
      axios.post('/auth/login', {
        data: {
          loginMethod: 'token',
          token: token
        },
      })
        .then((res) => {
          if (res.data.status !== 'success') {
            delete axios.defaults.headers.common['Authorization'];
            Auth.removeToken();
            // redirect to login
            setIsLoading(false);
            history.replace('/signin');
          } else {
            // console.log(res.data.data);
            axios.defaults.headers.common['Authorization'] = token;
            axios.get(`/user/${res.data.data.uid}`)
              .then((res) => {
                if (res.data.status === 'success') {
                  setUser(res.data.data.user);
                  setIsLoading(false);
                }
              });
          }
        });
    }

  }, [history, setUser, location, setIsLoading]);

  return (
    <Box className={classes.root}>
      <NavBar
        isFixed={!!user.uid}
      >
        {user.uid && (
          <>
            <IconButton
              className={classes.menuButton}
              color="inherit"
              aria-label="account menu"
              aria-controls="account-menu"
              aria-haspopup="true"
              onClick={handleClick}
            >
              <AccountCircleIcon />
            </IconButton>

            <Menu
              id="account-menu"
              anchorEl={anchorEl}
              keepMounted
              open={Boolean(anchorEl)}
              onClose={() => handleClose(false)}
            >
              <MenuItem onClick={() => {
                handleClose(true);
              }}
              >
                Logout
            </MenuItem>
            </Menu>
          </>
        )}
      </NavBar>
      {!isLoading ? (
        (user && user.role === 3) ? (
          <Candidate user={user} />
        ) : ((user && user.role === 2) ? (
          <Recruiter user={user} />
        ) : ((user && user.role === 1) ? (
          <Admin user={user} />
        ) : (
            <GuestWelcome />
          )))
      ) : (
          <Loader />
        )}
    </Box>
  );
}

export default Home;